@font-face {
  font-family: "나눔바른고딕";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("나눔바른고딕"), local("NanumBarunGothic"), local("나눔바른고딕OTF"), local("NanumBarunGothicOTF"), local("애플 SD 산돌고딕 Neo"),
    local("Apple SD Gothic Neo"), local("Noto Sans"), local("Noto+Sans"), local("notokr-demilight"), local("나눔고딕"), local("NanumGothic"),
    local("맑은 고딕"), local("Malgun Gothic");
  src: url("https://puripia.com/Font/nanumbarungothicregular.eot"); /*For IE6~8*/
  src: local(☺), url("https://puripia.com/Font/nanumbarungothicregular.eot?#iefix") format("embedded-opentype"),
    url("https://puripia.com/font/nanumbarungothicregular.woff2") format("woff2"),
    url("https://puripia.com/font/nanumbarungothicregular.woff") format("woff"),
    url("https://puripia.com/font/nanumbarungothicregular.ttf") format("truetype"), url("https://puripia.com/Font/notokr-demilight.svg") format("svg");
}

html,
body {
  font-family: "애플 SD 산돌고딕 Neo", "Apple SD Gothic Neo", "Helvetica Neue", "Helvetica", "나눔바른고딕", "NanumBarunGothic", "나눔바른고딕OTF",
    "NanumBarunGothicOTF", "Noto Sans", "Noto+Sans", "notokr-demilight", "나눔고딕", "NanumGothic", "맑은 고딕", "Malgun Gothic", "sans-serif" !important;
  margin: 0;
  padding: 0;
  font-family: "Noto Sans KR", sans-serif;
  background-color: #141414;
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  color: white;
}

.medium-zoom-image--opened {
  z-index: 3;
}
.medium-zoom-overlay {
  z-index: 2;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
  border-radius: 15px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgb(50, 50, 50);
}
